.Modal {
    position: fixed;
    z-index: 500;
    background-color: white !important;
    width: 70%;
    color: black;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 15%;
    top: 30%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
}


@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}